<template>
  <div id="app">
    <!-- <h1>crosstalk</h1> -->
    <div id="buttons">
      
      <button id="fs-btn" @click.prevent="toggleFullscreen()">
        <span class="material-symbols-rounded">fullscreen</span>
      </button>
      
      <button @click.prevent="newRound()" :disabled="showView">
        new round
      </button>
  
      <button @click.prevent="showView = !showView">
        toggle screen
      </button>
    </div>

    <div id="desc">
      <!-- click anywhere or press space to generate new words -->
    </div>

    <div class="container">
      <div 
          @touchstart.prevent="drag=true;pointDial($event)"
          @touchmove.prevent="pointDial($event)"
          @touchend.prevent="drag=false;"

          @mousedown.prevent="drag=true;pointDial($event)"
          @mousemove.prevent="pointDial($event)"  
          @mouseup.prevent="drag=false"
          id="spectrum">
        <div :style="specInnerStyle" class="spec-inner" >
          <div class="spec-slice base l"></div>
          <div class="spec-slice big l"></div>
          <div class="spec-slice med l"></div>
          <div class="spec-slice small"></div>
          <div class="spec-slice med r"></div>
          <div class="spec-slice big r"></div>
          <div class="spec-slice base r"></div>
        </div>
        <transition name="fade">
          <div v-if="showView" class="spec-view">
          </div>
        </transition>
        <div class="spec-border"></div>      
        <div class="spec-extra l"></div>
        <div class="spec-extra r"></div>
        
        <div :style="dialStyle" id="dial">
          <div ref="knob" id="dial-knob"></div>
          <div id="dial-needle"></div>
        </div>
      </div>
    </div>

    <transition-group name="card" id="cards" :class="{ selected: selectedCard} ">
      <div class="card" v-for="(card, i) in cards" :key="card.left+card.right">
        <div class="card-left"  
          @click.prevent="selectCard(i)"
          :style="card.leftStyle">{{card.left}}</div>
        <div class="card-right" 
          @click.prevent="selectCard(i)"
          :style="card.rightStyle">{{card.right}}</div>
      </div>
    </transition-group>

  </div>
</template>

<script>
// using raw-loader in webpack: see vue.config.js
import wordlist from './assets/words_codenames.txt'
import cards from './assets/cards.json'

export default {
  name: 'app',
  data() {
    return {
      cards: [],
      cardStyles: [], // MOVE into cards to not change 
      selectedCard: false,
      seenCards: [],

      wordlist: [],
      specInnerStyle: {
        transform: ""
      },
      dialStyle: {
        transform: ""
      },
      showView: false,
      drag: false,
    }
  },

  created() {
    // get wordlist
    this.wordlist = wordlist.trim().split('\n')
    // window.console.log(cards)
  },
  mounted() {
    window.addEventListener('keydown', (ev)=> {
      if (ev.key == " ") {
        this.showView = !this.showView
      } else if (ev.key == "r") {
        // this.newRound()
      }
    })

    this.newRound()
  },

  
  
  methods: {
    sleep: function(ms) {
      return new Promise(rs => setTimeout(rs, ms));
    },

    randColorStyle: function() {
      return {
        'background-color': this.genColor()
      }
    },

    randRange: function(min, max) {
      return Math.random() * (max - min) + min
    },

    // given polar angle, return rotate string
    polarRotateString: function(angle) {
      if (angle < 0)
        return 'rotate(' + (-angle) + 'deg)'
      else
        return 'rotate(-' + angle + 'deg)'

    },

    genColor: function() {
      var h = this.randRange(1, 360)
      var s = this.randRange(0, 100)
      var l = this.randRange(60, 90)
      return 'hsl(' + h + ',' + s + '%,' + l + '%)'
    },

    toggleFullscreen: function() {
      const span = document.querySelector('#fs-btn span')

      if (document.fullscreenElement) {
        // exit fullscreen if in fullscreen
        document.exitFullscreen()
        screen.orientation.unlock()
        span.textContent = 'fullscreen'
        
      } else {
        // enter fullscreen
        document.querySelector('#app').requestFullscreen()
        screen.orientation.lock('landscape')
        span.innerHTML = 'fullscreen_exit'
      }
    },

    newRound: async function() {
      // disallow new round when view is hidden
      if (this.showView) return

      this.showView = true
      this.genCards()
      this.selectedCard = false

      await this.sleep(1000)
      this.newAngle()


      // hide the dial
      this.dialStyle['transform'] = this.polarRotateString(200)
    },

    newAngle: function() {

      // generate new angle for the wavelength spectrum
      const minAngle = 13.5 // min angle for blue to show
      const maxAngle = 180 - minAngle
      const angle = this.randRange(minAngle, maxAngle)


      // this.specInnerStyle['transform'] = 'rotate(-' + minAngle + 'deg)'
      this.specInnerStyle['transform'] = this.polarRotateString(angle)
      // window.console.log("new angle")
    },

    pointDial: function(ev) {
            
      if (!this.drag) return

      let point

      if (ev.type.includes("touch")) {
        // window.console.log("touch")
        point = ev.targetTouches[0]
      } else {
        // window.console.log("mouse")
        point = ev
      }

      // window.console.log(ev)


      // window.console.log("pointing dial")
      // window.console.log(this.$refs.knob.getBoundingClientRect())

      const knob = this.$refs.knob.getBoundingClientRect()

      const dialX = knob.x + knob.width/2
      const dialY = knob.y + knob.height/2

      const dx = point.clientX - dialX
      const dy = point.clientY - dialY

      const angle = 180/Math.PI * Math.atan2(-dy, dx)

      // window.console.log(angle)

      this.dialStyle['transform'] = this.polarRotateString(angle-90)
      // this.dialStyle['transform'] = this.polarRotateString(20)

      // window.console.log(this.dialStyle)

    },

    genCards: function() {
      const numCards = 2

      // need to pick all or more
      if (numCards >= cards.length) return cards

      // window.console.log(cards.length, this.seenCards.length)

      // seen all cards
      if (cards.length - this.seenCards.length < numCards) {
        window.console.log("ran out of deck! need to reset deck")
        return
      }

      let pickedCards = []
      while (pickedCards.length < numCards) {
        let pick = cards[Math.floor(Math.random() * cards.length)]

        // no dupes from picked cards or previously seen cards
        while (pickedCards.includes(pick) || this.seenCards.includes(pick)) {
          pick = cards[Math.floor(Math.random() * cards.length)]
        }

        pickedCards.push(pick)
        this.seenCards.push(pick) // permanently add card to seen (refresh to reset)
      }

      // gen random color for each card half
      this.cardStyles = []
      for (let i=0; i<numCards*2; i++) {
        this.cardStyles.push(this.randColorStyle())
      }

      for (const [i, c] of pickedCards.entries()) {
        c.leftStyle = this.cardStyles[2*i]
        c.rightStyle = this.cardStyles[2*i+1]
      }

      // window.console.log(pickedCards)
      this.cards = pickedCards
    

    },

    selectCard: function(i) {
      this.cards = [this.cards[i]]
      this.selectedCard = true
      this.showView = false

    },
  },
}




</script>

<style lang="scss">

